.contact {
  .wpcf7-form-wrap {
    @include media-breakpoint-down(sm) {
      padding: 0; } }
  .wpcf7-form {
    background: $site-block-bg url('data:image/png;base64,');
    color: $clear;
    padding: 60px;
    @include media-breakpoint-down(md) {
      padding: 30px; }
    @include media-breakpoint-down(sm) {
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
      padding: 30px 15px; }
    &.sent {
      .wpcf7-response-output {
        // color: $valid
        color: $yellow; } }
    &.invalid {
      .wpcf7-response-output {
        color: $not-valid; } }
    &.submitting {
      .cf7-loader {
        display: block; } }
    .form-title {
      color: $clear; }
    input,
    textarea {
      background-color: transparent;
      font-weight: 300;
      font-size: 14px;
      line-height: 20px;
      color: $clear;
      padding: 17px;
      border: 1px solid $input-border;
      margin-bottom: 20px;
      width: 100%;
      &::placeholder {
        color: $placeholder; } }
    textarea {
      height: 130px;
      margin-bottom: 30px; }
    .wpcf7-not-valid {
      color: $not-valid;
      &::placeholder {
        color: $not-valid; } }
    .site-btn {
      margin-left: 10px;
      // display: block
      // width: fit-content
 }      // margin: 0 auto
    .cf7-loader {
      width: 100%;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      display: none; }
    .wpcf7-not-valid-tip {
      display: none; }
    .wpcf7-response-output {
      font-size: 14px;
      margin: 0;
      padding: 0;
      border: none;
      text-align: center; } }

  // Contact-Block-Wrap
  .contact-block-wrap {
    padding-top: 110px;
    @include media-breakpoint-down(md) {
      padding-top: 50px; }
    @include media-breakpoint-down(sm) {
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
      padding-top: 0;
      padding-bottom: 20px; } }
  .block-title {
    text-transform: none;
    @include media-breakpoint-down(sm) {
      text-align: center; }
    &::before {
      left: 33%;
      @include media-breakpoint-down(md) {
        left: 50%; } } }
  .contact-block {
    padding: 40px 0;
    border-bottom: 1px solid $border;
    @include media-breakpoint-down(sm) {
      padding: 20px 0; }
    &:last-of-type {
      border: none; }
    .img-wrap {
      border-radius: 50%;
      border: 1px solid $yellow;
      margin-right: 30px;
      width: 128px;
      height: 128px;
      @include media-breakpoint-down(md) {
        width: 100px;
        height: 100px;
        margin-right: 20px; } }
    &-img {
      border-radius: 50%;
      border: 4px solid $clear;
      @include media-breakpoint-down(md) {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-width: 2px; } }
    .name {
      padding-bottom: 2px; }
    .position {
      margin-bottom: 15px; }
    .position,
    .phone,
    .email {
      display: block;
      @include media-breakpoint-down(sm) {
        font-size: 14px; } }
    .phone,
    .email {
      color: $title-c;
      margin-bottom: 6px;
      .link-icon {
        margin: 0 5px 2px 0; } } } }





